  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 3em;  
  margin-top: 10%;
  -ms-flex-pack: center;
      justify-content: center;
}
.img-styles{
  border: 1px solid black;
  width: 100%;
  height: 100%;
  border-radius: 10%
}


